.menu-icon {
    transform: scale(.3);
    width: 30px;
    stroke: $concrete-white;
    margin-inline: auto 3px;
    top: -38px;
    right: 12px;

    &:hover {
        .menu-btn {
            stroke: $hover-color;
        }
    }

    .menu-btn {
        transition: stroke .2s;
        background-color: transparent;
        border: none;
        cursor: pointer;
        display: flex;
        padding: 0;

        svg:focus {
            outline: none;
        }
    }

    .line {
        fill: none;
        stroke: inherit;
        stroke-width: 6;
        transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
            stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .line1 {
        stroke-dasharray: 60 207;
        stroke-width: 6;
    }

    .line2 {
        stroke-dasharray: 60 60;
        stroke-width: 6;
    }

    .line3 {
        stroke-dasharray: 60 207;
        stroke-width: 6;
    }

    .opened .line1 {
        stroke-dasharray: 90 207;
        stroke-dashoffset: -134;
        stroke-width: 6;
    }

    .opened .line2 {
        stroke-dasharray: 1 60;
        stroke-dashoffset: -30;
        stroke-width: 6;
    }

    .opened .line3 {
        stroke-dasharray: 90 207;
        stroke-dashoffset: -134;
        stroke-width: 6;
    }
}